<template>
  <div class="home">
    <keep-alive>
    <transition name="route" mode="out-in">
        <router-view v-if="$route.path !== '/home'"> </router-view>
        </transition>
    </keep-alive>
    <div  v-if="$route.path !== '/home/step_five'" class="preview">
      <div style="margin-top: 20px" v-show="!this.side">
      <div class="img front">
        <div v-if="$store.state.Front.boolean" id="preview_img_front" class="preview_img" v-bind:style="{ 'background-image': 'url(' + $store.state.Front.url + ')' }">

        </div>
      </div>
      </div>
      <div v-show="side">
      <div class="img back">
        <div v-if="$store.state.Back.boolean" id="preview_img_back" class="preview_img" v-bind:style="{ 'background-image': 'url(' + $store.state.Back.url + ')' }">
        </div>
      </div>
      </div>
    </div>
    <div class="turn60">
      <label  v-if="$route.path !== '/home/step_five'"><input class="turnimg" v-model="side" type="checkbox"></label>
     <div class="total">
    <span v-if="$route.path !== '/home/step_five'">
    Aktualna Cena:
    </span>
    <span v-if="$route.path === '/home/step_five'">
    Razem:
     </span>
      {{ $store.state.Total }}
    </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'HomeView',
  data () {
    return {
      side: false
    }
  },
  methods: {
    modPreviewImg (i, bool, counter, scale, counterScale) {
      if (!bool) {
        switch (i) {
          case 1:
            if (counter) {
              document.getElementById('preview_img_front').style.filter = 'grayscale(' + scale + ')' + 'blur(' + counterScale + 'px)'
            } else {
              document.getElementById('preview_img_front').style.filter = 'grayscale(' + scale + ')'
            }
            break
          case 2:
            if (counter) {
              document.getElementById('preview_img_front').style.filter = 'blur(' + scale + 'px)' + 'grayscale(' + counterScale + ')'
            } else {
              document.getElementById('preview_img_front').style.filter = 'blur(' + scale + 'px)'
            }
            break
          case 3:
            if (counter) {
              document.getElementById('preview_img_back').style.filter = 'grayscale(' + scale + ')' + 'blur(' + counterScale + 'px)'
            } else {
              document.getElementById('preview_img_back').style.filter = 'grayscale(' + scale + ')'
            }
            break
          case 4:
            if (counter) {
              document.getElementById('preview_img_back').style.filter = 'blur(' + scale + 'px)' + 'grayscale(' + counterScale + ')'
            } else {
              document.getElementById('preview_img_back').style.filter = 'blur(' + scale + 'px)'
            }
            break
        }
      } else {
        switch (i) {
          case 1:
            if (counter) {
              document.getElementById('preview_img_front').style.filter = 'blur(' + counterScale + 'px)'
            } else {
              document.getElementById('preview_img_front').style.filter = 'none'
            }
            break
          case 2:
            if (counter) {
              document.getElementById('preview_img_front').style.filter = 'grayscale(' + counterScale + ')'
            } else {
              document.getElementById('preview_img_front').style.filter = 'none'
            }
            break
          case 3:
            if (counter) {
              document.getElementById('preview_img_back').style.filter = 'blur(' + counterScale + 'px)'
            } else {
              document.getElementById('preview_img_back').style.filter = 'none'
            }
            break
          case 4:
            if (counter) {
              document.getElementById('preview_img_back').style.filter = 'grayscale(' + counterScale + ')'
            } else {
              document.getElementById('preview_img_back').style.filter = 'none'
            }
            break
        }
      }
    }
  }
}
</script>
