<template>
<div>
<router-link to="/home/step_one"><div @click="updateRandom()"><button>START!</button></div></router-link>
</div>
</template>
<style lang="sass" scoped>
div
  height: 100vh
  button
    position: relative
    top: 40%
    color: white
    font-size: 8em
    text-decoration: none
    background: transparent
    border: none
    cursor: pointer
    transition: 1s
    animation: start 1s ease-out
    opacity: 1
    transform: translateY(0px)
    &:hover
      color: #4dde9f
@keyframes start
  from
    opacity: 0
    top: 35%
  to
    opacity: 1
    top: 40%
</style>
<script>
export default {
  name: 'HomeStart',
  methods: {
    updateRandom () {
      const frontID = Math.floor(Math.random() * 100)
      const backID = Math.floor(Math.random() * 100)
      this.$store.state.Front.url = this.$store.state.Base + frontID.toString()
      this.$store.state.Front.baseId = frontID
      this.$store.state.Back.url = this.$store.state.Base + backID.toString()
      this.$store.state.Back.baseId = backID
    }
  }
}
</script>
