import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    Base: 'https://picsum.photos/300/300?image=',
    Front:
      {
        title: 'Przód',
        boolean: false,
        baseId: '',
        url: 'https://picsum.photos/seed/picsum/300/300',
        price: 10,
        gray: {
          boolean: false,
          price: 2,
          scale: 0.1
        },
        blur: {
          boolean: false,
          price: 3,
          scale: 1
        }
      },
    Back:
      {
        title: 'Tył',
        boolean: false,
        baseId: '',
        url: 'https://picsum.photos/seed/picsum/300/300',
        price: 10,
        gray: {
          boolean: false,
          price: 2,
          scale: 0.1
        },
        blur: {
          boolean: false,
          price: 3,
          scale: 1
        }
      },
    PersonalData:
    {
    },
    Delivery:
    {
    },
    DeliveryChoice: '',
    Total: 0
  },
  mutations: {
    updateUrlFront (state, idFront) {
      const FrontUrl = idFront + state.Front.baseId
      state.Front.url = state.Base + FrontUrl
    },
    updateUrlBack (state, idBack) {
      const BackUrl = idBack + state.Back.baseId
      state.Back.url = state.Base + BackUrl
    },
    updateToCartSummary (state) {
      if (!state.Front.boolean) {
        state.Front.gray.boolean = state.Front.boolean
        state.Front.blur.boolean = state.Front.boolean
      }
      if (!state.Front.boolean) {
        state.Back.gray.boolean = state.Back.boolean
        state.Back.blur.boolean = state.Back.boolean
      }
    },
    updatePrice (state, deliveryPrice) {
      if (state.Front.boolean || state.Back.boolean) {
        if (state.Front.boolean && state.Back.boolean) {
          if (state.Front.gray.boolean || state.Front.blur.boolean || state.Back.gray.boolean || state.Back.blur.boolean) {
            if (state.Front.gray.boolean && state.Front.blur.boolean && state.Back.gray.boolean && state.Back.blur.boolean) {
              state.Total = state.Front.gray.price + state.Front.blur.price + state.Back.gray.price + state.Back.blur.price + state.Front.price + state.Back.price + deliveryPrice
            } else {
              if (state.Front.gray.boolean && state.Front.blur.boolean && state.Back.gray.boolean && !state.Back.blur.boolean) {
                state.Total = state.Front.gray.price + state.Front.blur.price + state.Back.gray.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (state.Front.gray.boolean && state.Front.blur.boolean && !state.Back.gray.boolean && state.Back.blur.boolean) {
                state.Total = state.Front.gray.price + state.Front.blur.price + state.Back.blur.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (state.Front.gray.boolean && !state.Front.blur.boolean && state.Back.gray.boolean && state.Back.blur.boolean) {
                state.Total = state.Front.gray.price + state.Back.gray.price + state.Back.blur.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (!state.Front.gray.boolean && state.Front.blur.boolean && state.Back.gray.boolean && state.Back.blur.boolean) {
                state.Total = state.Front.blur.price + state.Back.gray.price + state.Back.blur.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (state.Front.gray.boolean && state.Front.blur.boolean && !state.Back.gray.boolean && !state.Back.blur.boolean) {
                state.Total = state.Front.gray.price + state.Front.blur.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (!state.Front.gray.boolean && !state.Front.blur.boolean && state.Back.gray.boolean && state.Back.blur.boolean) {
                state.Total = state.Back.gray.price + state.Back.blur.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (!state.Front.gray.boolean && state.Front.blur.boolean && state.Back.gray.boolean && !state.Back.blur.boolean) {
                state.Total = state.Front.blur.price + state.Back.gray.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (state.Front.gray.boolean && !state.Front.blur.boolean && state.Back.gray.boolean && !state.Back.blur.boolean) {
                state.Total = state.Front.gray.price + state.Back.gray.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (!state.Front.gray.boolean && state.Front.blur.boolean && !state.Back.gray.boolean && state.Back.blur.boolean) {
                state.Total = state.Front.blur.price + state.Back.blur.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (state.Front.gray.boolean && !state.Front.blur.boolean && !state.Back.gray.boolean && state.Back.blur.boolean) {
                state.Total = state.Front.gray.price + state.Back.blur.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (state.Front.gray.boolean && !state.Front.blur.boolean && !state.Back.gray.boolean && !state.Back.blur.boolean) {
                state.Total = state.Front.gray.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (!state.Front.gray.boolean && state.Front.blur.boolean && !state.Back.gray.boolean && !state.Back.blur.boolean) {
                state.Total = state.Front.blur.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (!state.Front.gray.boolean && !state.Front.blur.boolean && state.Back.gray.boolean && !state.Back.blur.boolean) {
                state.Total = state.Back.gray.price + state.Front.price + state.Back.price + deliveryPrice
              }
              if (!state.Front.gray.boolean && !state.Front.blur.boolean && !state.Back.gray.boolean && state.Back.blur.boolean) {
                state.Total = state.Back.blur.price + state.Front.price + state.Back.price + deliveryPrice
              }
            }
          } else {
            state.Total = state.Front.price + state.Back.price + deliveryPrice
          }
        } else {
          if (state.Front.boolean) {
            if (state.Front.gray.boolean || state.Front.blur.boolean) {
              if (state.Front.gray.boolean && state.Front.blur.boolean) {
                state.Total = state.Front.price + state.Front.gray.price + state.Front.blur.price + deliveryPrice
              } else {
                if (state.Front.gray.boolean) {
                  state.Total = state.Front.price + state.Front.gray.price + deliveryPrice
                } else {
                  state.Total = state.Front.price + state.Front.blur.price + deliveryPrice
                }
              }
            } else {
              state.Total = state.Front.price + deliveryPrice
            }
          } else {
            if (state.Back.gray.boolean || state.Back.blur.boolean) {
              if (state.Back.gray.boolean && state.Back.blur.boolean) {
                state.Total = state.Back.price + state.Back.gray.price + state.Back.blur.price + deliveryPrice
              } else {
                if (state.Back.gray.boolean) {
                  state.Total = state.Back.price + state.Back.gray.price + deliveryPrice
                } else {
                  state.Total = state.Back.price + state.Back.blur.price + deliveryPrice
                }
              }
            } else {
              state.Total = state.Back.price + deliveryPrice
            }
          }
        }
      } else {
        state.Total = 0
      }
    }
  }
})
